import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';


@Component({
  selector: 'app-historia-clinica-form-paraclinicos',
  templateUrl: './historia-clinica-form-paraclinicos.component.html',
  styleUrls: ['./historia-clinica-form-paraclinicos.component.css']
})
export class HistoriaClinicaFormParaclinicosComponent implements OnInit {

  pacientes: any = [];
  edit = false;
  link = '';

  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('', [Validators.required]),
      anticuerpo_contra_sars_cov2: new FormControl('' ),
      fecha_hora_toma_muestra_anticuerpo: new FormControl('' ),
      igm: new FormControl('' ),
      igg: new FormControl('' ),
      prueba_embarazo: new FormControl('' ),
      fecha_hora_toma_muestra_antigeno: new FormControl('' ),
      observacion: new FormControl('' ),
      prueba_pcr: new FormControl('' ),
      fecha_hora_toma_muestra_pcr: new FormControl('' ),
      hgb: new FormControl('' ),
      serologias: new FormControl('' ),
      vih: new FormControl('' ),
      vdrl: new FormControl('' ),
      hepatitisb: new FormControl('' ),
      hepatitisc: new FormControl('' ),
      radiografia_torax: new FormControl('' ),
        
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
      
    
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      this.link = params.id;
                  
              this.comboListService.getParaclinico(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                      paciente_id:this.pacientes.paciente_id,
                      anticuerpo_contra_sars_cov2:this.pacientes.anticuerpo_contra_sars_cov2,
                      fecha_hora_toma_muestra_anticuerpo:this.pacientes.fecha_hora_toma_muestra_anticuerpo,
                      igm:this.pacientes.igm,
                      igg:this.pacientes.igg,
                      prueba_embarazo:this.pacientes.prueba_embarazo,
                      fecha_hora_toma_muestra_antigeno:this.pacientes.fecha_hora_toma_muestra_antigeno,
                      observacion:this.pacientes.observacion,
                      prueba_pcr:this.pacientes.prueba_pcr,
                      fecha_hora_toma_muestra_pcr:this.pacientes.fecha_hora_toma_muestra_pcr,
                      hgb:this.pacientes.hgb,
                      serologias:this.pacientes.serologias,
                      vih:this.pacientes.vih,
                      vdrl:this.pacientes.vdrl,
                      hepatitisb:this.pacientes.hepatitisb,
                      hepatitisc:this.pacientes.hepatitisc,
                      radiografia_torax:this.pacientes.radiografia_torax
                      
                      
                
                    
                      //imagen_qr: this.vacunas.imagen_qr,
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
           
    }


    Guardar() {
      const params = this.activatedRoute.snapshot.params;

      this.Form.get('paciente_id').setValue(params.id);
      
      this.comboListService.saveParaclinicos(this.Form.value).subscribe(
        res => {
              console.log(res);
             //this.router.navigate(['/tiposvacunas/admin/list']);
              Swal.fire(
                'Exitoso!',
                'Datos guardados correctamente...!',
                'success'
              );
              this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        } 
      );
    }

    Actualizar() {

      this.comboListService.updateParaclinicos(this.pacientes.id, this.pacientes)
        .subscribe(
          res => {
            console.log('actualizando', res);
           // this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos actualizados correctamente...!',
              'success'
            );
            this.ngOnInit();
          },
          err => {
            console.error(err);
            alert('Error!');

          }
        );
    }


}
