import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import * as moment from 'moment';

@Component({
  selector: 'app-criterio-exclusion-form-visitas',
  templateUrl: './criterio-exclusion-form-visitas.component.html',
  styleUrls: ['./criterio-exclusion-form-visitas.component.css']
})
export class CriterioExclusionFormVisitasComponent implements OnInit {
  pacientes: any = [];
  edit = false;
  link = '';


  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('', [Validators.required]),
      criterioex1: new FormControl('',),  
      criterioex2: new FormControl('',), 
      criterioex3: new FormControl('',), 
      criterioex4: new FormControl('',), 
      criterioex5: new FormControl('',), 
      criterioex6: new FormControl('',), 
      criterioex7: new FormControl('',), 
      criterioex8: new FormControl('',), 
      criterioex9: new FormControl('',), 
      criterioex10: new FormControl('',), 
      criterioex11: new FormControl('',), 
      criterioex12: new FormControl('',), 
      criterioex13: new FormControl('',),
      criterioex14: new FormControl('',),
      criterioex15: new FormControl('',)
 

          
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
  {
    
  
    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    this.link = params.id;
                
            this.comboListService.getCriterioExclusion(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.pacientes = res;
                  this.Form.patchValue({
                    paciente_id:this.pacientes.paciente_id,
                    criterioex1:this.pacientes.criterioex1,
                    criterioex2:this.pacientes.criterioex2,
                    criterioex3:this.pacientes.criterioex3,
                    criterioex4:this.pacientes.criterioex4,
                    criterioex5:this.pacientes.criterioex5,
                    criterioex6:this.pacientes.criterioex6,
                    criterioex7:this.pacientes.criterioex7,
                    criterioex8:this.pacientes.criterioex8,
                    criterioex9:this.pacientes.criterioex9,
                    criterioex10:this.pacientes.criterioex10,
                    criterioex11:this.pacientes.criterioex11,
                    criterioex12:this.pacientes.criterioex12,
                    criterioex13:this.pacientes.criterioex13,
                    criterioex14:this.pacientes.criterioex14,
                    criterioex15:this.pacientes.criterioex15,
                   
                    
                         
                    //imagen_qr: this.vacunas.imagen_qr,
                    
                  });

                  this.edit = true;
                
                },
                err => console.log(err)
              );
         
  }


  Guardar() {
    const params = this.activatedRoute.snapshot.params;

    this.Form.get('paciente_id').setValue(params.id);
    
    this.comboListService.saveCriterioExclusions(this.Form.value).subscribe(
      res => {
            console.log(res);
           //this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
            this.ngOnInit();
      },
      err => {
        console.error(err);
        alert('Error!');

      } 
    );
  }

  Actualizar() {

    this.comboListService.updateCriterioExclusions(this.pacientes.id, this.pacientes)
      .subscribe(
        res => {
          console.log('actualizando', res);
         // this.router.navigate(['/tiposvacunas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
          this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        }
      );
  }

}
