import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import * as moment from 'moment';


@Component({
  selector: 'app-historia-clinica-form-categorizacion-riesgo-infeccion',
  templateUrl: './historia-clinica-form-categorizacion-riesgo-infeccion.component.html',
  styleUrls: ['./historia-clinica-form-categorizacion-riesgo-infeccion.component.css']
})
export class HistoriaClinicaFormCategorizacionRiesgoInfeccionComponent implements OnInit {
  currentDate = moment().format("YYYY-MM-DD"); //Fecha Actual
  pacientes: any = [];
  edit = false;
  link = '';

  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('', [Validators.required]),
      opciones_riesgo: new FormControl(''),

            
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
      //this.fechaActual=new Date();
     
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      this.link = params.id;
           
              this.comboListService.getCategoriaInfeccion(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                     
                      paciente_id: this.pacientes.paciente_id,
                      opciones_riesgo: this.pacientes.opciones_riesgo,
                  
                      
                
                    
                      //imagen_qr: this.vacunas.imagen_qr,
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
          }
  
    
          Guardar() {
            const params = this.activatedRoute.snapshot.params;
      
            this.Form.get('paciente_id').setValue(params.id);
            
            this.comboListService.saveCategoriaInfeccions(this.Form.value).subscribe(
              res => {
                    console.log(res);
                   //this.router.navigate(['/tiposvacunas/admin/list']);
                    Swal.fire(
                      'Exitoso!',
                      'Datos guardados correctamente...!',
                      'success'
                    );
                    this.ngOnInit();
              },
              err => {
                console.error(err);
                alert('Error!');
      
              } 
            );
          }
      
          Actualizar() {
      
            this.comboListService.updateCategoriaInfeccions(this.pacientes.id, this.pacientes)
              .subscribe(
                res => {
                  console.log('actualizando', res);
                 // this.router.navigate(['/tiposvacunas/admin/list']);
                  Swal.fire(
                    'Exitoso!',
                    'Datos actualizados correctamente...!',
                    'success'
                  );
                  this.ngOnInit();
                },
                err => {
                  console.error(err);
                  alert('Error!');
      
                }
              );
          }


}
