import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CombolistService } from 'src/app/services/combolist.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-menu-visitas',
  templateUrl: './menu-visitas.component.html',
  styleUrls: ['./menu-visitas.component.css']
})
export class MenuVisitasComponent implements OnInit {

  VacunasMenu: boolean = false;
  UsuariosMenu: boolean = false;
  link = ''; 
  pacientes: any = [];


 


  constructor(
    private auth: UserService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,
  ) { }

  ngOnInit(): void {
    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    this.link = params.id;

    this.comboListService.getPaciente(params.id).subscribe(res => {
      this.pacientes = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );


  }

  logout() {
    this.auth.logout();
  }




}
