import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import * as moment from 'moment';


@Component({
  selector: 'app-criterio-inclusion-form-visitas',
  templateUrl: './criterio-inclusion-form-visitas.component.html',
  styleUrls: ['./criterio-inclusion-form-visitas.component.css']
})
export class CriterioInclusionFormVisitasComponent implements OnInit {

  currentDate = moment().format("YYYY-MM-DD"); //Fecha Actual
  pacientes: any = [];
  edit = false;
  link = '';


  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('', [Validators.required]),
      criterioin1: new FormControl('',),  
      criterioin2: new FormControl('',), 
      criterioin3: new FormControl('',), 
      criterioin4: new FormControl('',), 
      criterioin5: new FormControl('',), 
      criterioin6: new FormControl('',), 
      criterioin7: new FormControl('',), 
      criterioin8: new FormControl('',), 
      criterioin9: new FormControl('',), 
      criterioin10: new FormControl('',), 
      criterioin11: new FormControl('',), 
      criterioin12: new FormControl('',), 
      criterioin13: new FormControl('',)
 

          
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
  {
    
  
    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    this.link = params.id;
                
            this.comboListService.getCriterioInclusion(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.pacientes = res;
                  this.Form.patchValue({
                    paciente_id:this.pacientes.paciente_id,
                    criterioin1:this.pacientes.criterioin1,
                    criterioin2:this.pacientes.criterioin2,
                    criterioin3:this.pacientes.criterioin3,
                    criterioin4:this.pacientes.criterioin4,
                    criterioin5:this.pacientes.criterioin5,
                    criterioin6:this.pacientes.criterioin6,
                    criterioin7:this.pacientes.criterioin7,
                    criterioin8:this.pacientes.criterioin8,
                    criterioin9:this.pacientes.criterioin9,
                    criterioin10:this.pacientes.criterioin10,
                    criterioin11:this.pacientes.criterioin11,
                    criterioin12:this.pacientes.criterioin12,
                    criterioin13:this.pacientes.criterioin13,
                   
                    
                         
                    //imagen_qr: this.vacunas.imagen_qr,
                    
                  });

                  this.edit = true;
                
                },
                err => console.log(err)
              );
         
  }


  Guardar() {
    const params = this.activatedRoute.snapshot.params;

    this.Form.get('paciente_id').setValue(params.id);
    
    this.comboListService.saveCriterioInclusions(this.Form.value).subscribe(
      res => {
            console.log(res);
           //this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
            this.ngOnInit();
      },
      err => {
        console.error(err);
        alert('Error!');

      } 
    );
  }

  Actualizar() {

    this.comboListService.updateCriterioInclusions(this.pacientes.id, this.pacientes)
      .subscribe(
        res => {
          console.log('actualizando', res);
         // this.router.navigate(['/tiposvacunas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
          this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        }
      );
  }
}
