import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CombolistService } from 'src/app/services/combolist.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';



@Component({
  selector: 'app-pacientes-list-admin',
  templateUrl: './pacientes-list-admin.component.html',
  styleUrls: ['./pacientes-list-admin.component.css']
})
export class PacientesListAdminComponent implements OnInit {

  pacientes: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];

  constructor(private comboListService: CombolistService,
    private auth: UserService,
    private router: Router ) { }

  ngOnInit(): void {
    this.getVacunados();
  }

  getVacunados()
  {
    this.comboListService.getPacientes().subscribe(res => {
      this.pacientes = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }
}
