import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';


@Component({
  selector: 'app-historia-clinica-form-antecedentes-personales',
  templateUrl: './historia-clinica-form-antecedentes-personales.component.html',
  styleUrls: ['./historia-clinica-form-antecedentes-personales.component.css']
})
export class HistoriaClinicaFormAntecedentesPersonalesComponent implements OnInit {
  pacientes: any = [];
  edit = false;

  link = '';


  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('',),
      patalogicos: new FormControl('',),
      gineco_obstetrico : new FormControl('',),
      interrogatorios_aparatos : new FormControl('',),
 
        
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
      
      
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro

            this.link = params.id;
            //alert(this.link);
            this.comboListService.getAntecedentePersonal(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                      patalogicos: this.pacientes.patalogicos,
                      gineco_obstetrico:this.pacientes.gineco_obstetrico,
                      interrogatorios_aparatos:this.pacientes.interrogatorios_aparatos
                                       
                      //imagen_qr: this.vacunas.imagen_qr,
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
          
              
    }


    


    Guardar() {
      const params = this.activatedRoute.snapshot.params;

      this.Form.get('paciente_id').setValue(params.id);
      
      this.comboListService.saveAntecedentePersonals(this.Form.value).subscribe(
        res => {
              console.log(res);
             //this.router.navigate(['/tiposvacunas/admin/list']);
              Swal.fire(
                'Exitoso!',
                'Datos guardados correctamente...!',
                'success'
              );
              this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        } 
      );
    }

    Actualizar() {

      this.comboListService.updateAntecedentePersonals(this.pacientes.id, this.pacientes)
        .subscribe(
          res => {
            console.log('actualizando', res);
           // this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos actualizados correctamente...!',
              'success'
            );
            this.ngOnInit();
          },
          err => {
            console.error(err);
            alert('Error!');

          }
        );
    }


}
