import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import * as moment from 'moment';



@Component({
  selector: 'app-visitas3-paciente-form',
  templateUrl: './visitas3-paciente-form.component.html',
  styleUrls: ['./visitas3-paciente-form.component.css']
})
export class Visitas3PacienteFormComponent implements OnInit {

  currentDate = moment().format("YYYY-MM-DD"); //Fecha Actual
  pacientes: any = [];
  edit = false;
  link = '';

  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('', [Validators.required]),
      inmunidad_celular_humoral: new FormControl('' ),
      fecha_inmunidad_celular_humoral: new FormControl('' ),
     

          
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
  {
    
  
    const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
    this.link = params.id;
                
            this.comboListService.getVisitaTre(params.id) // obtengo el juego con el parametro del id
              .subscribe(
                res => { // aqui tomo las respuesta
                  console.log(res);
                  this.pacientes = res;
                  this.Form.patchValue({
                    paciente_id:this.pacientes.paciente_id,
                    inmunidad_celular_humoral: this.pacientes.inmunidad_celular_humoral,
                    fecha_inmunidad_celular_humoral: this.pacientes.fecha_inmunidad_celular_humoral,
               
                    
                         
                    //imagen_qr: this.vacunas.imagen_qr,
                    
                  });

                  this.edit = true;
                
                },
                err => console.log(err)
              );
         
  }


  Guardar() {
    const params = this.activatedRoute.snapshot.params;

    this.Form.get('paciente_id').setValue(params.id);
    
    this.comboListService.saveVisitaTres(this.Form.value).subscribe(
      res => {
            console.log(res);
           //this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
            this.ngOnInit();
      },
      err => {
        console.error(err);
        alert('Error!');

      } 
    );
  }

  Actualizar() {

    this.comboListService.updateVisitaTres(this.pacientes.id, this.pacientes)
      .subscribe(
        res => {
          console.log('actualizando', res);
         // this.router.navigate(['/tiposvacunas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
          this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        }
      );
  }
}
