import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';

import * as moment from 'moment';



@Component({
  selector: 'app-historia-clinica-form-proceso-aleatorizacion',
  templateUrl: './historia-clinica-form-proceso-aleatorizacion.component.html',
  styleUrls: ['./historia-clinica-form-proceso-aleatorizacion.component.css']
})
export class HistoriaClinicaFormProcesoAleatorizacionComponent implements OnInit {
  currentDate = moment().format("YYYY-MM-DD"); //Fecha Actual

  pacientes: any = [];
  edit = false;
  link = '';


  createFormGroup() {
    return new FormGroup({
      paciente_id: new FormControl('', [Validators.required]),
      ingreso_proceso: new FormControl('' ),
      fecha_proceso: new FormControl('' ),
      hora_proceso: new FormControl('' ),
      numero_aleatoriazacion: new FormControl('' ),
      realizado_por: new FormControl('' ),
      notas: new FormControl('' ),
          
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
      //this.fechaActual=new Date();
     
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      this.link = params.id;
            
              this.comboListService.getProcesoAletorizacion(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                     
                      paciente_id: this.pacientes.paciente_id,
                      ingreso_proceso: this.pacientes.ingreso_proceso,
                      fecha_proceso: this.pacientes.fecha_proceso,
                      hora_proceso: this.pacientes.hora_proceso,
                      numero_aleatoriazacion: this.pacientes.numero_aleatoriazacion,
                      realizado_por: this.pacientes.realizado_por,
                      notas: this.pacientes.notas
                
                    
                      //imagen_qr: this.vacunas.imagen_qr,
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
          }
  
    
          Guardar() {
            const params = this.activatedRoute.snapshot.params;
      
            this.Form.get('paciente_id').setValue(params.id);
            
            this.comboListService.saveProcesoAletorizacions(this.Form.value).subscribe(
              res => {
                    console.log(res);
                   //this.router.navigate(['/tiposvacunas/admin/list']);
                    Swal.fire(
                      'Exitoso!',
                      'Datos guardados correctamente...!',
                      'success'
                    );
                    this.ngOnInit();
              },
              err => {
                console.error(err);
                alert('Error!');
      
              } 
            );
          }
      
          Actualizar() {
      
            this.comboListService.updateProcesoAletorizacions(this.pacientes.id, this.pacientes)
              .subscribe(
                res => {
                  console.log('actualizando', res);
                 // this.router.navigate(['/tiposvacunas/admin/list']);
                  Swal.fire(
                    'Exitoso!',
                    'Datos actualizados correctamente...!',
                    'success'
                  );
                  this.ngOnInit();
                },
                err => {
                  console.error(err);
                  alert('Error!');
      
                }
              );
          }




}
