import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import * as moment from 'moment';

@Component({
  selector: 'app-historia-clinica-varias-telemedicinas-form',
  templateUrl: './historia-clinica-varias-telemedicinas-form.component.html',
  styleUrls: ['./historia-clinica-varias-telemedicinas-form.component.css']
})
export class HistoriaClinicaVariasTelemedicinasFormComponent implements OnInit {
  currentDate = moment().format("YYYY-MM-DD"); //Fecha Actual
  pacientes: any = [];
   edit = false;
  link = '';

  telemedicinas: any = [];
  filter: '';
  paginate = 1;



  createFormGroup() {
    return new FormGroup({
     paciente_id: new FormControl('', [Validators.required]),
     campo_telemedicina: new FormControl('', [Validators.required] ),
     });
  }

  Form: FormGroup;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }



  ngOnInit(): void {
    this.getTelemedicinas();
  }


  getTelemedicinas()
  {
    this.comboListService.getVariasTelemedicinas().subscribe(res => {
      this.telemedicinas = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }

  onReset(){
    this.Form.reset();
  }


  Guardar() {
    const params = this.activatedRoute.snapshot.params;

    this.Form.get('paciente_id').setValue(params.id);

    this.comboListService.saveVariasTelemedicinas(this.Form.value).subscribe(
      res => {
            console.log(res);
           //this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos guardados correctamente...!',
              'success'
            );
            this.onReset();
            this.ngOnInit();
      },
      err => {
        console.error(err);
        alert('Error!');

      }
    );
  }

  Actualizar() {

    this.comboListService.updateVariasTelemedicinas(this.pacientes.id, this.pacientes)
      .subscribe(
        res => {
          console.log('actualizando', res);
         // this.router.navigate(['/tiposvacunas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
          this.ngOnInit();
        },
        err => {
          console.error(err);
          alert('Error!');

        }
      );
  }

}
