import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';


@Component({
  selector: 'app-historia-clinica-form-proceso-vacunacion',
  templateUrl: './historia-clinica-form-proceso-vacunacion.component.html',
  styleUrls: ['./historia-clinica-form-proceso-vacunacion.component.css']
})
export class HistoriaClinicaFormProcesoVacunacionComponent implements OnInit {

  pacientes: any = [];
  edit = false;

  link = '';


  createFormGroup() {
    return new FormGroup({
      paciente_cedula: new FormControl('', [Validators.required]),
      ingreso_vacunacion: new FormControl('' ),
      fecha_vacunacion: new FormControl('' ),
      hora_vacunacion: new FormControl('' ),
      numero_vacunacion: new FormControl('' ),
      realizado_por_vacunacion: new FormControl('' ),
      notas_vacunacion: new FormControl('' ),
       
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
      //this.fechaActual=new Date();
    
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      this.link = params.id;
             
              this.comboListService.getProcesoVacunacion(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                      cedula:this.pacientes.cedula,
                     paciente_id: this.pacientes.paciente_id,
                     ingreso_vacunacion: this.pacientes.ingreso_vacunacion,
                     fecha_vacunacion: this.pacientes.fecha_vacunacion,
                     hora_vacunacion: this.pacientes.hora_vacunacion,
                     numero_vacunacion: this.pacientes.numero_vacunacion,
                     realizado_por_vacunacion: this.pacientes.realizado_por_vacunacion,
                     notas_vacunacion: this.pacientes.notas_vacunacion
                  
                    
                      //imagen_qr: this.vacunas.imagen_qr,
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
          }
  



          Guardar() {
            const params = this.activatedRoute.snapshot.params;
      
            this.Form.get('paciente_id').setValue(params.id);
            
            this.comboListService.saveProcesoVacunacions(this.Form.value).subscribe(
              res => {
                    console.log(res);
                   //this.router.navigate(['/tiposvacunas/admin/list']);
                    Swal.fire(
                      'Exitoso!',
                      'Datos guardados correctamente...!',
                      'success'
                    );
                    this.ngOnInit();
              },
              err => {
                console.error(err);
                alert('Error!');
      
              } 
            );
          }
      
          Actualizar() {
      
            this.comboListService.updateProcesoVacunacions(this.pacientes.id, this.pacientes)
              .subscribe(
                res => {
                  console.log('actualizando', res);
                 // this.router.navigate(['/tiposvacunas/admin/list']);
                  Swal.fire(
                    'Exitoso!',
                    'Datos actualizados correctamente...!',
                    'success'
                  );
                  this.ngOnInit();
                },
                err => {
                  console.error(err);
                  alert('Error!');
      
                }
              );
          }
          







          
          
          

}
