import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import * as moment from 'moment';



@Component({
  selector: 'app-historia-clinica-form-paciente',
  templateUrl: './historia-clinica-form-paciente.component.html',
  styleUrls: ['./historia-clinica-form-paciente.component.css']
})
export class HistoriaClinicaFormPacienteComponent implements OnInit {
  currentDate = moment().format("YYYY-MM-DD"); //Fecha Actual
  pacientes: any = [];
  edit = false;

  link = '';


  createFormGroup() {
    return new FormGroup({
      cedula: new FormControl('', [Validators.required]),
      nombres: new FormControl('', [Validators.required]),
      apellidos: new FormControl('', [Validators.required]),
      fecha_nacimiento: new FormControl('', [Validators.required]),
      sexo: new FormControl('', [Validators.required]),
      raza: new FormControl('', [Validators.required]),
      domicilio_actual: new FormControl('', [Validators.required]),
      telefono_local: new FormControl('' ),
      telefono_celular: new FormControl('' ),
      email: new FormControl('' ),
        
      //imagen_qr: new FormControl(''),
     });
  }

  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit() 
    {
      
    
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      this.link = params.id;
      if (params.id)
            {
              this.comboListService.getPaciente(params.id) // obtengo el juego con el parametro del id
                .subscribe(
                  res => { // aqui tomo las respuesta
                    console.log(res);
                    this.pacientes = res;
                    this.Form.patchValue({
                      cedula:this.pacientes.cedula,
                      nombres:this.pacientes.nombres,
                      apellidos:this.pacientes.apellidos,
                      fecha_nacimiento:this.pacientes.fecha_nacimiento,
                      sexo:this.pacientes.sexo,
                      raza:this.pacientes.raza,
                      domicilio_actual:this.pacientes.domicilio_actual,
                      telefono_local:this.pacientes.telefono_local,
                      telefono_celular:this.pacientes.telefono_celular,
                      email:this.pacientes.email
                      
                
                    
                      //imagen_qr: this.vacunas.imagen_qr,
                      
                    });

                    this.edit = true;
                  
                  },
                  err => console.log(err)
                );
          }
  
    }

    menu()
    {
      const params = this.activatedRoute.snapshot.params; // para saber si recibo un parametro
      this.link = params.id;
      //alert(this.link); 
    }

    Actualizar() {

      this.comboListService.updatePacientes(this.pacientes.id, this.pacientes)
        .subscribe(
          res => {
            console.log('actualizando', res);
           // this.router.navigate(['/tiposvacunas/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Datos actualizados correctamente...!',
              'success'
            );
          },
          err => {
            console.error(err);
            alert('Error!');

          }
        );
    }

}
