import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { map } from 'rxjs/operators';
import { CombolistService } from 'src/app/services/combolist.service';
import { VacunasService } from 'src/app/services/vacunas.service';


@Component({
  selector: 'app-historia-clinica-cedula-form',
  templateUrl: './historia-clinica-cedula-form.component.html',
  styleUrls: ['./historia-clinica-cedula-form.component.css']
})
export class HistoriaClinicaCedulaFormComponent implements OnInit {
  pacientes: any = [];
  cedulaRegistrada: any = [];
  pacienteGuardar: any = [];
  cedula_paciente = '';
  ocultar = false;
  botonH = false;
  cedulados: any = [];

  createFormGroup() {
    return new FormGroup({
      cedula: new FormControl('', [Validators.required]),
      nombres : new FormControl('', [Validators.required]),
      apellidos : new FormControl('', [Validators.required]),
      fecha_nacimiento : new FormControl('', [Validators.required]),
    
     });
  }


  Form: FormGroup;


  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private comboListService: CombolistService,
    private vacunasService: VacunasService,

  ) {  this.Form = this.createFormGroup(); }

  ngOnInit(): void {
  }



  verificarCedula()
  {
    
    this.comboListService.getPacienteCedulaRegistrada(this.pacientes.cedula).subscribe(res => {
      if(Object.keys(res).length >= 1)
      {
        this.cedulaRegistrada = res;
        this.cedula_paciente = this.cedulaRegistrada[0].id;
        //alert(this.cedula_paciente);
        //alert('La cedula se encuentra registrada en el sistema');
        Swal.fire(
          'Exitoso!',
          'La Cedula Se Encuentra Registrada En El Sistema!!!',
          'success'
        );
        console.log('excel:', res);
        this.ocultar = false;
        this.botonH = true;
      }

      else 
      {
        alert('!!Cedula no esta registrada');
        this.ocultar = true;
        this.botonH = false;
        this.vacunasService.getCeduladosSaime(this.pacientes.cedula, 'V') // obtengo el juego con el parametro del id
                      .subscribe(
                        res => { // aqui tomo las respuesta

                          if(Object.keys(res).length >= 1) 
                          {
                          console.log('Cedulados: ', res);
                          this.cedulados = res;
                          
                          this.cedulados.forEach(element => {
                               
                         this.pacientes.nombres = `${element.primernombre} ${element.segundonombre}`;
                          this.pacientes.apellidos = `${element.primerapellido} ${element.segundoapellido}`;
                          this.pacientes.fecha_nacimiento = element.fechanac;
    
                          

                         
                         
                      });
                        }

                        
                        },
                        err => console.log(err)
                      );




      }
    
    },
    err => {
      console.log(err);
    }
   );
  }






  Guardar() {


    this.comboListService.getPacienteCedulaRegistrada(this.pacientes.cedula).subscribe(res => {
      if(Object.keys(res).length >= 1)
      {
      
        alert('La cedula Ya  se encuentra registrada en el sistema');
        this.onReset();
       
        
      }

      else 
      {
        this.comboListService.savePacientes(this.Form.value).subscribe(
          res => {
                console.log(res);
                this.pacienteGuardar = res;
                console.log('Codigo paciente:', this.pacienteGuardar.id);
                this.router.navigate(['/historia_clinica/admin/form/paciente', this.pacienteGuardar.id]);
                Swal.fire(
                  'Exitoso!',
                  'Datos guardados correctamente...!',
                  'success'
                );
          },
          err => {
            console.error(err);
            alert('Error!');
    
          } 
        );
      }
    
    },
    err => {
      console.log(err);
    }
   );


  }


  onReset() {
     
    this.Form.reset();
}

}
